import React, { useMemo, useRef } from "react";
import { OrbitControls, Sky } from "@react-three/drei";
import {
  Physics,
  RigidBody,
  CuboidCollider,
  InstancedRigidBodies,
  // CuboidCollider
} from "@react-three/rapier";
import * as THREE from "three";
import { useFrame } from "@react-three/fiber";

function Experience() {
  const cube = useRef();

  const cubes = useRef();

  const twister = useRef();

  const cubeJump = () => {
    cube.current.applyImpulse({ x: 0, y: 20, z: 0 });
    cube.current.applyTorqueImpulse({
      x: (Math.random() - 0.5) * 4,
      y: (Math.random() - 0.5) * 4,
      z: (Math.random() - 0.5) * 4,
    });
  };

  // useEffect(() => {
  //   for (let i = 0; i < cubesCount; i++) {
  //     const matrix = new THREE.Matrix4();
  //     matrix.compose(
  //       new THREE.Vector3(i * 2, 0, 0),
  //       new THREE.Quaternion(),
  //       new THREE.Vector3(1, 1, 1)
  //     );
  //     cubes.current.setMatrixAt(i, matrix);
  //   }
  // }, []);

  const cubesCount = 300;
  const cubeTransforms = useMemo(() => {
    const positions = [];
    const rotations = [];
    const scales = [];

    for (let i = 0; i < cubesCount; i++) {
      positions.push([
        (Math.random() - 0.5) * 4,
        6 + i * 0.2,
        (Math.random() - 0.5) * 4,
      ]);
      rotations.push([Math.random(), Math.random(), Math.random()]);
      scales.push([
        Math.random() + 0.2,
        Math.random() + 0.2,
        Math.random() + 0.2,
      ]);
    }
    return { positions, rotations, scales };
  });

  // const [sound, setSound] = useState(() => {
  //   return new Audio("./hit.mp3");
  // });

  useFrame((state, delta) => {
    const clock = state.clock.elapsedTime;
    const angle = new THREE.Euler(0, clock * 5, 0);
    const quaterAngle = new THREE.Quaternion();
    quaterAngle.setFromEuler(angle);
    twister.current.setNextKinematicRotation(quaterAngle);
    twister.current.setNextKinematicTranslation({
      x: Math.sin(clock * 0.5) * 3,
      y: -0.4,
      z: Math.cos(clock * 0.5) * 3,
    });
  });

  // const model = useGLTF("./hamburger.glb");

  // const collisionDetected = () => {
  //   sound.currentTime = 0;
  //   sound.volume = Math.random();
  //   sound.play();
  // };

  return (
    <>
      <Sky></Sky>
      <OrbitControls makeDefault />
      <ambientLight intensity={0.5} />
      <directionalLight castShadow instensity={1.5} position={[1, 2, 3]} />
      <Physics>
        {/* <Debug /> */}
        <RigidBody type="fixed">
          <CuboidCollider args={[5, 3, 0.5]} position={[0, 2.36, 5.5]} />
          <CuboidCollider args={[5, 3, 0.5]} position={[0, 2.36, -5.5]} />
          <CuboidCollider args={[0.5, 3, 5]} position={[5.5, 2.36, 0]} />
          <CuboidCollider args={[0.5, 3, 5]} position={[-5.5, 2.36, 0]} />
        </RigidBody>
        <RigidBody colliders="ball">
          <mesh castShadow position={[-1.5, 2, 0]}>
            <sphereGeometry />
            <meshStandardMaterial color={"orange"} />
          </mesh>
        </RigidBody>
        <RigidBody colliders={"cuboid"} ref={cube} position={[1.5, 2, 0]}>
          <mesh scale={1.5} onClick={cubeJump}>
            <boxGeometry />
            <meshStandardMaterial color={"mediumpurple"} />
          </mesh>
        </RigidBody>

        <RigidBody
          ref={twister}
          position={[0, -0.4, 0]}
          friction={0}
          type="kinematicPosition"
        >
          <mesh castShadow scale={[0.4, 0.4, 3]}>
            <boxGeometry />
            <meshStandardMaterial color="red" />
          </mesh>
        </RigidBody>

        <RigidBody type={"fixed"}>
          <mesh
            receiveShadow
            position={[0, -1, 0]}
            scale={[10, 10, 0.75]}
            rotation-x={-Math.PI * 0.5}
          >
            <boxGeometry />
            <meshStandardMaterial color={"yellowgreen"} />
          </mesh>
        </RigidBody>
        {/* <RigidBody colliders="trimesh" position={[0, 5, 0]}>
          <primitive object={model.scene} scale={0.25} />
        </RigidBody> */}

        <InstancedRigidBodies
          positions={cubeTransforms.positions}
          rotations={cubeTransforms.rotations}
          scales={cubeTransforms.scales}
        >
          <instancedMesh ref={cubes} args={[null, null, cubesCount]}>
            <boxGeometry />
            <meshStandardMaterial color="tomato" />
          </instancedMesh>
        </InstancedRigidBodies>
      </Physics>
    </>
  );
}

export default Experience;
